@import "./colors";

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family:'Open Sans', sans-serif;
  line-height: 1.2;
  letter-spacing: 1.2px;
}

img {
  display: block;
  max-width: 100%;
}

h1 {
  font-size: 2.2rem;
  font-weight: 700;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.1rem;
  font-weight: 400;
}

.btn {
  display: inline-block;
  cursor: pointer;
  transition: .4s ease;
  font-weight: 700;
}

@media (min-width: 720px) {
  h1 {
    font-size: 2.5rem;
  }
  h2 {
    font-size: 1.8rem;
  }
  h3 {
    font-size: 1.3rem;
  }
}

@media (min-width: 1040px) {
  h1 {
    font-size: 2.8rem;
  }
  h2 {
    font-size: 2rem;
  }
  h3 {
    font-size: 1.5rem;
  }
}